<template>
  <div class="hello">
    <!-- <van-picker
  show-toolbar
  :columns="columns"
  @cancel="onCancel"
  @confirm="onConfirm"
/> -->
<button @click="a()">按钮</button>
<van-popup v-model="show" position="bottom" :overlay="true">
  <van-picker
    show-toolbar
    :columns="columns"
    @cancel="onCancel"
    @confirm="onConfirm"
  />
</van-popup>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
  data() {
    return {
      columns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
      show:false
    }
  },
  methods: {
    a(){
      this.show = true
    },
    onConfirm(value, index) {
      this.show = false
      Toast(`当前值：${value}, 当前索引：${index}`);
    },
    onCancel() {
      this.show = false
      Toast('取消');
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



